import React, { useState, Fragment, useRef, useContext } from "react";
import { FetchUrl } from "../../store/communication";
import AuthContext from "../../store/auth-context";
import { useNavigate } from "react-router-dom";

import "./SocialLogin.css"; //  CSS  

function SocialLogin() {
  const FETCH_URL = useContext(FetchUrl);

  const [selectSex, setSelectSex] = useState("ND");
  const navigate = useNavigate();

  const handleSelectSex = (e) => {
    setSelectSex(e.target.value);
  };

  const nameInputRef = useRef();
  const nickNameInputRef = useRef();
  const sexInputRef = useRef();
  const birthdayInputRef = useRef();

  const authCtx = useContext(AuthContext);

  const submitHandler = (event) => {
    event.preventDefault();

    const enteredName = nameInputRef.current.value;
    const enteredNickname = nickNameInputRef.current.value;
    const enteredSex = sexInputRef.current.value;
    const enteredBirthday = birthdayInputRef.current.value;

    const url = `${FETCH_URL}/social-signup`;
    // Interacting with server
    fetch(url, {
      method: "POST",
      withCredentials: true,
      body: JSON.stringify({
        name: enteredName,
        nickName: enteredNickname,
        gender: enteredSex,
        birth: enteredBirthday,
      }),
      headers: {
        "content-type": "application/json",
      },
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response.json().then((data) => {
            let errorMessage = " ";
            throw new Error(errorMessage);
          });
        }
      })
      .then((result) => {
        if (result != null) {
          authCtx.login();
          alert(" ");
          navigate("/");
          window.location.reload();
        }
      })
      .catch((err) => {
        alert(err.message);
      });
  };

  return (
    <Fragment>
      <div className="social-login">
        <form onSubmit={submitHandler}>
          <div className="social-login-top">
            <div className="social-login-top__word">
                  
            </div>
          </div>
          <div className="social-login-form">
            <div className="line">
              <input
                type="text"
                placeholder=" "
                required
                ref={nameInputRef}
              />
            </div>
            <div className="line">
              <input
                className="short"
                type="text"
                placeholder=" "
                required
                ref={nickNameInputRef}
              />
              <button className="dup"></button>
            </div>
            <div className="line">
              <select
                name="sex"
                placeholder=" "
                onChange={handleSelectSex}
                value={selectSex}
                required
                ref={sexInputRef}
              >
                <option value="M"></option>
                <option value="F"></option>
                <option value="ND"></option>
              </select>
            </div>
            <div className="line">
              <input
                type="date"
                placeholder=" "
                required
                min="1900-01-01"
                max="2022-12-31"
                ref={birthdayInputRef}
              />
            </div>
            <button className="submitting" type="submit">
              
            </button>
          </div>
        </form>
      </div>
    </Fragment>
  );
}

export default SocialLogin;
