import { useRef, useContext } from "react";
import AuthContext from "../../store/auth-context";
import { FetchUrl } from "../../store/communication";
import { Link, useNavigate } from "react-router-dom";
import "./Login.css";
import kakao from "../../img/SignUpLogos/kakao.png";
import naver from "../../img/SignUpLogos/naver1.png";
import google from "../../img/SignUpLogos/google.png";

const Login = () => {
  const FETCH_URL = useContext(FetchUrl);

  const navigate = useNavigate();

  const emailInputRef = useRef();
  const passwordInputRef = useRef();

  //using Context for token
  const authCtx = useContext(AuthContext);

  // Function handling submit button
  const submitHandler = (event) => {
    event.preventDefault();

    const enteredEmail = emailInputRef.current.value;
    const enteredPassword = passwordInputRef.current.value;

    const url = `${FETCH_URL}/login`;
    console.log(url);

    // Interacting with server
    fetch(url, {
      method: "POST",
      body: JSON.stringify({
        email: enteredEmail,
        password: enteredPassword,
      }),
      headers: {
        "content-type": "application/json",
      },
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response.json().then((data) => {
            let errorMessage = "Authentication failed!";
            throw new Error(errorMessage);
          });
        }
      })
      .then((result) => {
        document.cookie = `accessToken=${result.responseData.accessToken}`;
        authCtx.login();
        alert(" ");
        navigate("/");
        window.location.reload();
      })
      .catch((err) => {
        alert(err.message);
      });
  };

  // ! btn not yet finished
  return (
    <div className="login">
      <div className="login-title">Login</div>
      <form className="login-inputs" onSubmit={submitHandler}>
        <input
          className="input-box"
          type="email"
          placeholder=" "
          required
          ref={emailInputRef}
        />
        <input
          className="input-box"
          type="password"
          placeholder=" "
          required
          ref={passwordInputRef}
        />
        <a href="#none">/ </a>
        <button className="submit-btn"></button>
      </form>

      {/* <div className="insert-word">Or Continue With</div> */}

      <div className="signup-btns">
        <Link to="/signup">
          <div className="email"> </div>
        </Link>
        <a
          href={`${FETCH_URL}/oauth2/authorization/kakao?redirect_uri=https://watchme2.shop/api/login/oauth2/code/kakao`}
        >
          <div className="kakao">
            <img src={kakao} alt="" width={28} />
            <span> </span>
          </div>
        </a>
        <a
          href={`${FETCH_URL}/oauth2/authorization/naver?redirect_uri=https://watchme2.shop/api/login/oauth2/code/naver`}
        >
          <div className="naver">
            <img src={naver} alt="" width={40} />
            <span> </span>
          </div>
        </a>
        <a
          href={`${FETCH_URL}/oauth2/authorization/google?redirect_uri=https://watchme2.shop/api/login/oauth2/code/google`}
        >
          <div className="google">
            <img src={google} alt="" width={40} />
            <span>Google </span>
          </div>
        </a>
      </div>
    </div>
  );
};

export default Login;
