import React from "react";
import { useState,useContext,useRef } from "react";
import { Link,useNavigate  } from "react-router-dom";
import { FetchUrl } from "../../store/communication";

import "./RoomCreate.css";

function CreateRoom() {

  //  
  const [inputs, setInputs] = useState({
    "roomName":"",
    "status":"MODE1", //MODE1, MODE2, MODE3
    "roomPwd":"",
    "description":"",
    "categoryName":"", //TAG1, TAG2, TAG3
    "num":0,
    "roomPublic":"Private",
    "endTime":"",
    "display":1,
  });
  const navigate=useNavigate()

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs((values) => ({ ...values, [name]: value }));
  };

  //URL
  const FETCH_URL = useContext(FetchUrl);
  const url = `${FETCH_URL}/addRoom`;
  //Otpion

  const imgeRef=useRef();

  const handleSubmit = (event) => {
    event.preventDefault();

function getCookie(name) {
  const cookie = document.cookie
    .split(";").map((cookie) => cookie.split("="))
    .filter((cookie) => cookie[0] === name);
  return cookie[0][1];
}

const formData=new FormData();
formData.append('images', imgeRef.current.files[0]);
formData.append("postRoomReqDTO", new Blob([JSON.stringify(inputs)], {type: "application/json"}))

console.log(url);
fetch(url, {
  method: "POST",
  body: formData,
  headers:{
    accessToken: getCookie("accessToken")
  }
})
.then((response) => {
  if (response.ok) {
    
    console.log("C1");
    console.log(response);
    return response.json();//ok  .
  } else {
    response.json().then((data) => { 
      console.log("ERR");
      let errorMessage = "";
      throw new Error(errorMessage);
    });
  }
})
.then((result) => {
  if (result != null) {
    console.log(" ");
    console.log(result);
    console.log("CK");
    console.log(result["responseData"]["roomId"]);
    navigate("/RoomDetail/:"+result["responseData"]["roomId"]);
    // window.location.reload();//
  }
})
.catch((err) => {
  console.log("ERR");
});
  }
    

  const [fileImage, setFileImage] = useState("");
  const saveFileImage = (event) =>{
    setFileImage(URL.createObjectURL(event.target.files[0]));

  }
  return (
    <div className="body-frame">
      <Link to="/RoomRecruit" className="back-to-recruit">
        &lt;  
      </Link>

      <form onSubmit={handleSubmit}>
        {/*form input name, type  .   id   . input  .*/}
        <div className="form-frame">
          <div className="room-image">
            
          {fileImage && ( <img alt="sample" src={fileImage}style={{position:"absolute", marginTop: "55px" ,width:"150px",height:"150px",borderRadius:"50%" 
              }} /> )}
            {/* ,  */}
            <input
              type="file"
              name="roomImage"
              accept="image/*"
              onChange={saveFileImage}
              className="room-image__upload"
              ref={imgeRef}
            />
            <div className="room-image__message">  </div>
            
          </div>
          <div className="room-infor">
            {/**/}

            <div className="input-type">
              <div className="line">
                <input
                  type="text"
                  name="roomName"
                  value={inputs.roomName || ""}
                  onChange={handleChange}
                  placeholder="  "
                />
              </div>
              <div className="line">
                <input
                  type="text"
                  name="description"
                  value={inputs.description || ""}
                  onChange={handleChange}
                  placeholder="  "
                />
              </div>
              <div className="line">
                <input
                  type="number"
                  name="num"
                  value={inputs.num?inputs.num:"" }
                  onChange={handleChange}
                  accept="number"
                  placeholder=" (1~25)"
                />
                <select
                  name="categoryName"
                  value={inputs.categoryName || ""}
                  onChange={handleChange}
                >
                  <option value="" disabled>
                     
                  </option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                  <option value=""></option>
                </select>
              </div>
              <div className="line">
                <span></span>
                <label className="switch">
                  <input
                    type="checkbox"
                    name="roomPublic"
                    value={inputs.roomPublic || ""}
                    onChange={handleChange}
                  />
                  <span className="slider round"></span>
                </label>

                {/*checkbox   .*/}
                <input
                  type="text"
                  name="roomPwd"
                  value={inputs.roomPwd || ""}
                  onChange={handleChange}
                  maxLength="4"
                  minLength="4"
                  placeholder=" 4"
                />
              </div>
            </div>

            <div className="input-rules">
              {/*.  . */}
              <div className="rules-title"> </div>
              <div className="rules-box">
                {/*    .*/}
                <label>
                  <input type="checkbox" />
                   
                </label>
                <label>
                  <input type="checkbox" />
                   
                </label>
                <label>
                  <input type="checkbox" />
                  
                </label>
                <label>
                  <input type="checkbox" />
                  
                </label>
              </div>
            </div>
            <button type="submit"></button>
          </div>
        </div>
      </form>
    </div>
  );
}

export default CreateRoom;
