import React, { useState, Fragment, useRef, useContext } from "react";
import { useNavigate } from "react-router-dom";
import { FetchUrl } from "../../store/communication";

import "./SignUp.css";

function SignUp() {
  const FETCH_URL = useContext(FetchUrl);

  const [selectSex, setSelectSex] = useState("ND");
  const navigate = useNavigate();

  const handleSelectSex = (e) => {
    setSelectSex(e.target.value);
  };

  const emailInputRef = useRef();
  const passwordInputRef = useRef();
  const nameInputRef = useRef();
  const nickNameInputRef = useRef();
  const sexInputRef = useRef();
  const birthdayInputRef = useRef();
  const imageInputRef=useRef();

  const submitHandler = (event) => {
    event.preventDefault();
    const data={
      email: emailInputRef.current.value,
      password: passwordInputRef.current.value,
      name: nameInputRef.current.value,
      nickName: nickNameInputRef.current.value,
      gender: sexInputRef.current.value,
      birth: birthdayInputRef.current.value,
    };

    const files=imageInputRef.current.files[0];
    const formData = new FormData();
    formData.append('files', files);
    formData.append("data", new Blob([JSON.stringify(data)], {type: "application/json"}))
   
    //myjsons->application.josn
    

    const url = `${FETCH_URL}/signup`;
    // Interacting with server
    fetch(url, {
      method: "POST",
      body: formData
    })
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          response.json().then((data) => {
            let errorMessage = "Authentication failed!";
            throw new Error(errorMessage);
          });
        }
      })
      .then((result) => {
        alert(" ");
        navigate("/login"); // 
      })
      .catch((err) => {
        alert(err.message);
      });
  };


  const [fileImage, setFileImage] = useState("");
  const saveFileImage = (event) =>{
    setFileImage(URL.createObjectURL(event.target.files[0]));

  };
  return (
    <Fragment>
      <div className="signup">
        <form onSubmit={submitHandler} method="post" >
          <div className="signup-top">
            <div className="signup-top__worfd">SIGN UP</div>
          </div>
          <div className="signup-form">
            <div className="signup-left">
              <div className="signup-left-image"> 
                {fileImage && ( <img alt="sample" src={fileImage}style={{ margin: "auto" ,width:"100%",height:"100%",borderRadius:"50%"
              }} /> )}
              </div>
              <input  name="imggeUpload" type="file" accept="image/*" onChange={saveFileImage}  ref={imageInputRef}/>
              
              <button className="signup-left-addimage">  </button>

             
            </div>
            <div className="signup-right">
              <div className="line">
                <input
                  className="short"
                  type="email"
                  placeholder=" "
                  required
                  ref={emailInputRef}
                />
                <button className="dup"></button>
              </div>
              <div className="line">
                <input
                  className="half"
                  type="password"
                  placeholder=" "
                  required
                  ref={passwordInputRef}
                />
                <input
                  className="half"
                  type="password"
                  placeholder="  "
                />
              </div>
              <div className="line">
                <input
                  type="text"
                  placeholder=" "
                  required
                  ref={nameInputRef}
                />
              </div>
              <div className="line">
                <input
                  className="short"
                  type="text"
                  placeholder=" "
                  required
                  ref={nickNameInputRef}
                />
                <button className="dup"></button>
              </div>
              <div className="line">
                <select
                  name="sex"
                  placeholder=" "
                  onChange={handleSelectSex}
                  value={selectSex}
                  required
                  ref={sexInputRef}
                >
                  <option value="M"></option>
                  <option value="F"></option>
                  <option value="ND"></option>
                </select>
              </div>
              <div className="line">
                <input
                  type="date"
                  placeholder=" "
                  required
                  min="1900-01-01"
                  max="2022-12-31"
                  ref={birthdayInputRef}
                />
              </div>
              <button className="submitting" type="submit">
                
              </button>
            </div>
          </div>
        </form>
      </div>
    </Fragment>
  );
}

export default SignUp;
